import React from "react";
import './manger-de-saison.css';
import InfoProject from './../../assets/data/manger-de-saison.json';
import HeaderProject from "../../components/header_project/HeaderProject";
import PresentationProject from "../../components/presentation-project/PresentationProject";
import Carousel from "../../components/carousel/Carousel";
import { Link } from 'react-router-dom';
import { useEffect } from "react";
import { Container, Row, Col } from 'react-grid-system';
import Part02_Illu01 from './../../assets/illustrations/manger-de-saison/part02_illu01.png';
import Part02_Illu02 from './../../assets/illustrations/manger-de-saison/part02_illu02.png';
import Part02_Illu03 from './../../assets/illustrations/manger-de-saison/part02_illu03.png';
import Part03_Illu01 from './../../assets/illustrations/manger-de-saison/part03_illu01.png';
import { ReactComponent as Forward } from './../../assets/icons/forward.svg'
import Zoom from 'react-medium-image-zoom'
import 'react-medium-image-zoom/dist/styles.css'

export default function MangerDeSaison() {
    useEffect(() => {
        document.title = "Manger de saison - Tho Blandin";
    }, []);

    return (
        <>
            <HeaderProject project={InfoProject}></HeaderProject>

            <div className="my-5 d-flex flex-column gap-5">
                <PresentationProject
                    project={InfoProject}>
                </PresentationProject>

                <Carousel project={InfoProject}></Carousel>

                <Container className="container-explanations d-flex flex-column">
                    <Row justify="center">
                        <Col xl={7} xxl={6}>
                            <div className="section d-flex flex-column gap-4">
                                <div>
                                    <span className="display-S">01</span>
                                    <h1 style={{ marginTop: '-8px' }}>Benchmark</h1>
                                </div>
                                <div className="alinea">
                                    <p className="mb-2">Il existe plusieurs applications et sites web similaires  <i>Manger de saison</i>. J'en ai analys plusieurs, principalement francophones, pour avoir une vision claire de l'offre actuelle.</p>

                                    <p className="mb-2">Les retours utilisateurs sur ces applications mobiles ont galement t pris en compte pour enrichir mon tude.</p>
                                </div>
                            </div>
                        </Col>
                        <Col xl={2} xxl={3}></Col>
                    </Row>
                    <div className="section d-flex flex-column gap-4">
                        <Row justify="center">
                            <Col xl={7} xxl={6}>
                                <div className="section d-flex flex-column gap-4">
                                    <div>
                                        <span className="display-S">02</span>
                                        <h1 style={{ marginTop: '-8px' }}>Maquettage</h1>
                                    </div>
                                    <div className="alinea">
                                        <p className="mb-2">La premire version de la maquette de l'application a t ralise sur papier lors de la phase d'idation, puis transfre sur Figma. Elle n'est pas destine  tre reproduite exactement  l'identique, mais plutt  servir de point de dpart. Au fil du dveloppement, le design s'est affin paralllement  l'volution de mes comptences.</p>
                                    </div>
                                </div>
                            </Col>
                            <Col xl={2} xxl={3}></Col>
                        </Row>
                        <Row justify="center">
                            <Col xl={9} className="d-flex align-items-center justify-content-center gap-20 multiple-illu-container w-100">
                                <div className="d-flex flex-column align-items-center gap-2 d-flex flex-column align-items-cneter flex-1">
                                    <Zoom zoomMargin={32} a11yNameButtonUnzoom="Rduire l'image" a11yNameButtonZoom="Agrandir l'image">
                                        <img
                                            alt="Maquette sur papier de l'application avec annotations"
                                            src={Part02_Illu01}
                                            className="w-100"
                                        />
                                    </Zoom>
                                    <span aria-hidden="true" className="small">
                                        Maquette sur papier
                                    </span>
                                </div>
                                <div className="d-flex flex-column align-items-center gap-2 flex-1">
                                    <div className="double-illu d-flex gap-20 w-100">
                                        <Zoom zoomMargin={32} className="illu-of-double-illu">
                                            <img
                                                alt="Maquette sur Figma de l'cran d'accueil de l'application"
                                                src={Part02_Illu02}
                                                className="w-100"
                                            />
                                        </Zoom>

                                        <Zoom zoomMargin={32} className="illu-of-double-illu">
                                            <img
                                                alt="Maquette sur Figma de l'cran d'accueil de l'application avec un modal affichant les informations de l'lment slectionn"
                                                src={Part02_Illu03}
                                                className="w-100"
                                            />
                                        </Zoom>
                                    </div>
                                    <span aria-hidden="true" className="small">
                                        Maquette sur Figma
                                    </span>
                                </div>
                            </Col>
                        </Row>
                    </div>
                    <div className="section d-flex flex-column gap-4">
                        <Row justify="center">
                            <Col xl={7} xxl={6}>
                                <div className="section d-flex flex-column gap-4">
                                    <div>
                                        <span className="display-S">03</span>
                                        <h1 style={{ marginTop: '-8px' }}>Illustrations</h1>
                                    </div>
                                    <div className="alinea">
                                        <p className="mb-2">Les illustrations des diffrents fruits et lgumes recenss dans le calendrier furent ralises sous forme de dessin vectoriel  laide du logiciel Inkscape. L'objectif tant de crer un design attractif pour l'utilisateur, tout en lui permettant de reconnatre facilement chaque aliment.</p>

                                        <p className="mb-2">En attendant que l'ensemble des illustrations soit complt, des mojis sont employs, ou un point d'interrogation lorsque aucun moji n'est disponible dans la bibliothque utilise.</p>
                                    </div>
                                </div>
                            </Col>
                            <Col xl={2} xxl={3}></Col>
                        </Row>
                        <Row justify="center">
                            <Col xl={9} className="d-flex flex-column align-items-center gap-2">
                                <Zoom zoomMargin={32} a11yNameButtonUnzoom="Rduire l'image" a11yNameButtonZoom="Agrandir l'image">
                                    <img
                                        alt="Cration d'une illustration sur Inkscape"
                                        src={Part03_Illu01}
                                        className="w-100"
                                    />
                                </Zoom>
                                <span aria-hidden="true" className="small">
                                    Cration d'une illustration sur Inkscape
                                </span>
                            </Col>
                        </Row>
                    </div>
                    <Row justify="center">
                        <Col xl={7} xxl={6}>
                            <div className="section d-flex flex-column gap-4">
                                <div>
                                    <span className="display-S">04</span>
                                    <h1 style={{ marginTop: '-8px' }}>Dveloppement</h1>
                                </div>
                                <div className="alinea">
                                    <p className="mb-2"><i>Manger de Saison</i> est ma deuxime application mobile dveloppe avec Flutter, destine aux tlphones Android.</p>

                                    <p className="mb-2">Les donnes sur les aliments ainsi que les prfrences des utilisateurs sont stockes localement sur l'appareil, garantissant un accs continu aux informations tout en rduisant les cots.</p>
                                </div>
                            </div>
                        </Col>
                        <Col xl={2} xxl={3}></Col>
                    </Row>
                    <Row justify="center">
                        <Col xl={7} xxl={6}>
                            <div className="section d-flex flex-column gap-4">
                                <div>
                                    <span className="display-S">05</span>
                                    <h1 style={{ marginTop: '-8px' }}>Ce que a m'a apport</h1>
                                </div>
                                <div className="alinea">
                                    <p className="mb-2"><i>Manger de saison</i> est ma deuxime application mobile, un projet qui m'a permis de progresser  la fois en dveloppement, en approfondissant mes comptences en Flutter, et en design, notamment grce  la cration des illustrations des fruits et lgumes.</p>

                                    <p className="mb-2">Ce travail d'illustration, toujours en cours, m'a permis de mieux matriser le logiciel Inkscape tout en dveloppant mon propre style graphique. L'application a galement travers plusieurs refontes, refltant l'volution continue de mes comptences en design d'interface.</p>

                                    <p className="mb-2">Ce projet constitue pour moi un terrain d'apprentissage idal, alliant technique et crativit, que je continue  perfectionner avec l'objectif de le publier un jour.</p>
                                </div>
                            </div>
                        </Col>
                        <Col xl={2} xxl={3}></Col>
                    </Row>
                    <div className="d-flex flex-row justify-content-end align-items-center gap-2">
                        <Link to='/swap' className="d-flex flex-row justify-content-end align-items-center gap-2 py-4 next">
                            <span>Vers le prochain projet</span>
                            <Forward className="icon-M icon-dark" />
                        </Link>
                    </div>
                </Container>
            </div>
        </>
    )
}